import React from 'react';
import { Link } from 'react-router-dom';
import $ from 'jquery';

// import '../styles/Footer.scss';
// import '../styles/Containers.scss';

const About = props => (
  <div className="footer-item about">
    <div className="heading">
      <h1>About</h1>
      <img
        src="/images/horizontal-logo-white-h80.png"
        alt="Seoul Food Take Out Logo"
        className="logo"
      />
    </div>
    <p>
      Seoul Food Take-Out was born from <span>Mrs. Park</span>'s love of
      cooking. This isn't your typical restaurant, just a take-out only food
      stop with <span>Mrs. Park</span> as the sole chef. She pours her love and
      soul into every dish she makes. With generous portions and authentic
      Korean cooking, you don't want to miss out. Come visit the one and only
      Seoul Food Take-Out today, you won't regret it.
    </p>
    {!props.location.location.pathname.includes('menu') && (
      <Link
        className="amazingMenu"
        to="/menu/daily-special"
        onClick={e => {
          if (props.location.location.pathname.includes('menu')) {
            e.preventDefault();
          } else {
            $('.wrapper').animate({ scrollTop: 0 }, 0);
          }
        }}
      >
        Our Amazing Menu
      </Link>
    )}
  </div>
);

const Hours = props => (
  <div
    className={
      'footer-item hours ' +
      (props.location.location.pathname.includes('contact') && 'hide ')
    }
  >
    <h1>Business Hours</h1>
    <hr />
    <div className="time">
      <div>
        <span>Monday :</span>
        <span>8:00AM ~ 6:00PM</span>
      </div>
      <div>
        <span>Tuesday :</span>
        <span>8:00AM ~ 6:00PM</span>
      </div>
      <div>
        <span>Wednesday :</span>
        <span>8:00AM ~ 6:00PM</span>
      </div>
      <div>
        <span>Thursday :</span>
        <span>8:00AM ~ 6:00PM</span>
      </div>
      <div>
        <span>Friday :</span>
        <span>8:00AM ~ 6:00PM</span>
      </div>
      <div className="closed">
        <span>Saturday - Sunday :</span>
        <span>CLOSED</span>
      </div>
      <div className="closed">
        <span>Statutory Holidays :</span>
        <span>CLOSED</span>
      </div>
    </div>
  </div>
);

const Info = props => (
  <div
    className={
      'footer-item info ' +
      (props.location.location.pathname.includes('contact') && ' hide ')
    }
  >
    <h1>Store Info</h1>
    <div className="info-items">
      <p className="address info-item">
        <a
          href="https://www.google.com/maps/place/Seoul+Food+Take-Out/@43.6719658,-79.3768762,15z/data=!4m5!3m4!1s0x0:0xe57c456e3a4c6ee4!8m2!3d43.6719658!4d-79.3768762"
          target="blank"
          title="click for a larger map"
        >
          <i className="fas fa-map-marked-alt" />
        </a>
        606 Sherbourne St, Toronto, ON M4X-1X6
      </p>
      <p className="phone info-item">
        <a href="tel:4164500519" title="click to dial">
          <i className="fas fa-phone" />
        </a>
        (416) 450-0519
      </p>

      {!props.location.location.pathname.includes('contact') && (
        <Link
          className="moreInfo"
          to="/contact"
          onClick={e => {
            if (props.location.location.pathname.includes('contact')) {
              e.preventDefault();
            } else {
              $('.wrapper').animate({ scrollTop: 0 }, 0);
            }
          }}
        >
          More Information
        </Link>
      )}
    </div>
  </div>
);

const CopyRight = () => (
  <div className="copy-right">
    <p>
      Copyright &copy; {new Date().getFullYear()} Seoul Food Take-Out. All
      rights reserved.
    </p>
  </div>
);

const Footer = props => (
  <>
    <footer className="master-footer">
      <div className="seoulContainer">
        <div className="footer-header">
          <About location={props} />
          <Hours location={props} />
          <Info location={props} />
        </div>
      </div>
      <div className="seoulContainer-m to-top-box">
        <div className="to-top" onClick={props.totoTop}>
          <i className="fas fa-chevron-up" />
          Back to top
        </div>
      </div>
      <CopyRight />
    </footer>
  </>
);

export default Footer;
